import React from 'react';
import '../App.css';
import '../css/Header.css';
import logo from '../images/logo.png';

export const Header = (props) => {
    return (
        <header>
            <div className="container">
                <div className="row">
                    <div className="col-1"><img onClick={() => { props.menuCheckCall("isHome") }} className="logo" src={logo} alt="logo" />
                        <span className="site-desc">You Dream, We Digitalise Them . .</span>
                    </div>
                    <div className="col-2">
                        <nav>
                            <ul>
                                <li onClick={() => { props.menuCheckCall("isAbout") }}>About Us</li>
                                <li>Digital Commerce<span className="menu-arrow"></span>
                                    <div className="dropdown">
                                        <ul>
                                            <li onClick={() => { props.menuCheckCall("isHclCommerce") }}>HCL Commerce</li>
                                            <li onClick={() => { props.menuCheckCall("isSap") }}>SAP Hybris</li>
                                            <li onClick={() => { props.menuCheckCall("isAdobe") }}>Adobe AEM</li>
                                            <li onClick={() => { props.menuCheckCall("isMagento") }}>Magento</li>
                                            <li onClick={() => { props.menuCheckCall("isOracle") }}>Oracle ATG</li>
                                        </ul>
                                    </div>
                                </li>
                                <li>Cloud / AI<span className="menu-arrow"></span>
                                    <div className="dropdown">
                                        <ul>
                                            <li onClick={() => { props.menuCheckCall("isAWS") }}>aws</li>
                                            <li onClick={() => { props.menuCheckCall("isMicroSoft") }}>MicroSoft Azure</li>
                                            <li onClick={() => { props.menuCheckCall("isGoogleCloud") }}>Google Cloud</li>
                                            <li onClick={() => { props.menuCheckCall("isIBM") }}>IBM Cloud</li>
                                            <li onClick={() => { props.menuCheckCall("isSalesCloud") }}>Sales Cloud</li>
                                            <li onClick={() => { props.menuCheckCall("isAI") }}>Artificial Intelligence</li>
                                        </ul>
                                    </div>
                                </li>
                                <li>Buginess Units<span className="menu-arrow"></span>
                                    <div className="dropdown">
                                        <ul>
                                            <li>Management Consulting</li>
                                            <li>It buginess Unit</li>
                                        </ul>
                                    </div>
                                </li>
                                <li onClick={() => { props.menuCheckCall("isSuccessStory") }}>Success Stories</li>
                                <li onClick={() => { props.menuCheckCall("isContact") }}>Contact US</li>
                            </ul>
                        </nav>
                    </div>
                </div>
            </div>
        </header>
    )
}


