import React from 'react';
import '../css/Contact.css';
import { Implink } from '../component/Implink';
export const Contact = () => {
  return (
    <div className="contact">
      <div className="container">
        <div className="row">
          <div className="col-1">
            <p className="topparam">Interested in partnering with us&nbsp;? Get in touch and well get back to you as soon as
            we can.</p>
            <h2>Contact Form</h2>
            <p className="required">Fields marked with an&nbsp;*&nbsp;are required</p>
            <div className="row justify-content">
              <div className="col-1-1">
                <form action="" method="post">
                  <label for="first-name" className="coblocks-label">First Name <span className="required">*</span></label>
                  <input type="hidden" name="field-first-name[label]" value="First Name" />

                  <input type="hidden" id="name-field-id" name="name-field-id" className="coblocks-name-field-id"
                    value="field-first-name" />

                  <input type="text" id="first-name" name="field-first-name[value]"
                    className="coblocks-field coblocks-field--name" required="" />

                  <label for="last-name-2" className="coblocks-label">Last Name <span className="required">*</span></label>
                  <input type="hidden" name="field-last-name-2[label]" value="Last Name" />

                  <input type="hidden" id="name-field-id" name="name-field-id" className="coblocks-name-field-id"
                    value="field-last-name-2" />

                  <input type="text" id="last-name-2" name="field-last-name-2[value]"
                    className="coblocks-field coblocks-field--name" required="" />



                  <label for="work-email" className="coblocks-label">Work Email <span className="required">*</span></label>
                  <input type="hidden" name="field-work-email[label]" value="Work Email" />


                  <input type="hidden" id="email-field-id" name="email-field-id" className="coblocks-email-field-id"
                    value="field-work-email" />
                  <input type="email" id="work-email" name="field-work-email[value]"
                    className="coblocks-field coblocks-field--email" required="" />



                  <label for="company-3" className="coblocks-label">Company <span className="required">*</span></label>
                  <input type="hidden" name="field-company-3[label]" value="Company" />

                  <input type="hidden" id="name-field-id" name="name-field-id" className="coblocks-name-field-id"
                    value="field-company-3" />

                  <input type="text" id="company-3" name="field-company-3[value]"
                    className="coblocks-field coblocks-field--name" required="" />



                  <label for="country-4" className="coblocks-label">Country <span className="required">*</span></label>
                  <input type="hidden" name="field-country-4[label]" value="Country" />

                  <input type="hidden" id="name-field-id" name="name-field-id" className="coblocks-name-field-id"
                    value="field-country-4" />

                  <input type="text" id="country-4" name="field-country-4[value]"
                    className="coblocks-field coblocks-field--name" required="" />



                  <label for="message" className="coblocks-label">Message <span className="required">*</span></label>
                  <input type="hidden" name="field-message[label]" value="Message" />


                  <textarea name="field-message[value]" id="message" className="coblocks-field coblocks-textarea" rows="20"
                    required=""></textarea>
                  <button type="submit" className="submitbtn">Submit</button>
                </form>
              </div>
              <div className="col-1-2">
                <div className="">
                  <div className="">
                    <h3>Contact Details</h3>
                    <p className="email"><strong>Email:</strong> hr@ursag.com</p>
                    <p className="phone"><strong>Phone:</strong> +39-3343442465</p>
                    <h3 className="office">Offices</h3>
                    <h4>Milan</h4>
                    <p className="contact_data">Via Pietro Paleocapa 7<br></br>20121 Milan, Italy</p>
                    <h4>London</h4>
                    <p className="contact_data">161, City Road,<br></br>London, EC1V 2NX</p>
                    <h4>Gurugram</h4>
                    <p className="contact_data">90B, Udyog Vihar,<br></br>
                          Phase IV, Sector 18,<br></br>Gurugram, Haryana, India</p>
                    <h4>Bangalore</h4>
                    <p className="contact_data">510, Mahadevapura,<br></br>
                              Bangalore, Karnataka, India </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="col-2">
            <Implink />
          </div>
        </div>
      </div>
    </div >
  );
}


