import React from 'react';
import '../css/Magento.css';
import gallery1 from '../images/magentogallery1.jpg';
import gallery2 from '../images/magentogallery2.png';
import gallery3 from '../images/magentogallery3.jpg';
import gallery4 from '../images/magentogallery4.jpg';
import gallery5 from '../images/magentogallery5.jpg';
import gallery6 from '../images/magentogallery6.jpg';
import gallery7 from '../images/magentogallery7.jpg';
import gallery8 from '../images/magentogallery8.jpg';
import gallery9 from '../images/magentogallery9.jpg';
export const Magento = () => {
  return (
    <div className="magento">
      <div className="magento-back"></div>
      <div className="container">
        <p className="desc gray-color">Magento, an open source E-commerce software, has a flexible modular architecture. It is scalable and it has many control options that help the organisations to build both user-friendly and search engine friendly websites.</p>
        <blockquote className="blockquotewithborder"><p>Magento is simple, quick and versatile to use.</p></blockquote>
        <h2>Capabilities </h2>
        <div className="Capabilities">
          <div className="row justify-content">
            <div className="col om_back_color">
              <h4>Order Management</h4>
            </div>
            <div className="col bs_back_color">
              <h4>Business Intelligence</h4>
            </div>
          </div>
          <div className="col gsac_back_color">
            <h4>Google Shopping Ads Channel</h4>
          </div>
          <div className="row justify-content">
            <div className="col em_back_color">
              <h4>Extension Marketplace</h4>
            </div>
            <div className="col asc_back_color">
              <h4>Amazon Sales Channel</h4>
            </div>
          </div>
          <p className="desc gray-color">At URSAG, we analyse and evaluate each feature of product to make sure that our client is benifitted with the use of that product and none of the available feature is left untouched. Our skilled and motivated team towards greater client satisfaction and benefit. Below are the few features that we can avail using Magento Commerce .</p>
        </div>
        <div className="gallery">
          <ul>
            <li><img src={gallery1}></img></li>
            <li><img src={gallery2}></img></li>
            <li><img src={gallery3}></img></li>
            <li><img src={gallery4}></img></li>
            <li><img src={gallery5}></img></li>
            <li><img src={gallery6}></img></li>
            <li><img src={gallery7}></img></li>
            <li><img src={gallery8}></img></li>
            <li><img src={gallery9}></img></li>
          </ul>
        </div>
      </div>
    </div >
  );
}


