import React, { Component } from 'react';
import { Consumer } from './LinkContext';
export class Implink extends Component {
  constructor() {
    super()
    this.linkMenuFun = "";
  }
  render() {
    return (
      <React.Fragment>
        <Consumer>
          {
            (linkMenuCheck) => {
              this.linkMenuFun = linkMenuCheck
            }
          }
        </Consumer>
        <div className="imp_link">
          <h6 className="imp_title">Important Links</h6>
          <ul>
            <li>Adobe AEM</li>
            <li>Artificial Intelligence</li>
            <li>AWS</li>
            <li>Cloud Services</li>
            <li>Digital Commerce</li>
            <li>Google Cloud</li>
            <li>HCL Commerce</li>
            <li>IBM Cloud</li>
            <li>Magento</li>
            <li>MicroSoft Azure</li>
            <li>Oracle ATG</li>
            <li>Sales Cloud</li>
            <li onClick={() => { this.linkMenuFun("isSapHybris") }}>SAP Hybris</li>
            <li onClick={() => { this.linkMenuFun("isSuccessStory") }}>Success Stories</li>
          </ul>
        </div>
      </React.Fragment >
    )
  }
}


