import React from 'react';
import '../css/SuccessStory.css';
function SuccessStory() {
  return (
    <div className="success-story">
      <div className="title">
        <h1 className="main-title">SUCCESS STORIES</h1>
        <span className="line"></span>
      </div>
      <div className="stories-list">
        <div className="row justify-content">
          <div className="col-1 box-shadow">
            <h3>CATALOG REDESIGN OF LUXURY EYE-WARE BRAND</h3>
            <span className="desc">A luxury eye-ware brand wanted to redesign their catalog with the goal to
            provide a completely new look to websites Header Navigation, Promo Bar, Search Panel and
            Catalog View for user friendly experience and amazing appearance. With our great
                            people</span>
            <div className="read-more"><a className="btn more-btn" href="#">Read
                                More</a>
            </div>
          </div>
          <div className="col-2 box-shadow">
            <h3>CATALOG REDESIGN OF LUXURY EYE-WARE BRAND</h3>
            <span className="desc">A luxury eye-ware brand wanted to redesign their catalog with the goal to
            provide a completely new look to websites Header Navigation, Promo Bar, Search Panel and
            Catalog View for user friendly experience and amazing appearance. With our great
                            people</span>
            <div className="read-more"><a className="btn more-btn" href="#">Read
                                More</a>
            </div>
          </div>
          <div className="col-3 box-shadow">
            <h3>CATALOG REDESIGN OF LUXURY EYE-WARE BRAND</h3>
            <span className="desc">A luxury eye-ware brand wanted to redesign their catalog with the goal to
            provide a completely new look to websites Header Navigation, Promo Bar, Search Panel and
            Catalog View for user friendly experience and amazing appearance. With our great
                            people</span>
            <div className="read-more"><a className="btn more-btn" href="#">Read
                                More</a>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default SuccessStory;


