import React from 'react';
import '../css/SalesCloud.css';
import featureimg from '../images/features.png';
export const SalesCloud = () => {
  return (
    <div className="sales-cloud">
      <div className="sales-cloud-back"></div>
      <div className="container">
        <div className="content">
          <p className="desc gray-color">Sales Cloud is focused on enhancing the effectiveness of the sales team of an organization and hence increases the amount of sales. It stands unique when compared to other sales methods as it provides both the account information of the customer as well as the information gathered from the social platforms about the product and customer.</p>
          <div className="quote"><blockquote><div><i className="fa fa-quote-left"></i><p className="quotetitle">Sell better with Sales Cloud</p></div><cite>Sell better by getting to know your customers better.<br />Convert more leads, close more deals, and understand the health of your entire business with Sales Cloud</cite></blockquote></div>
          <h2>Features</h2>
          <div className="features">
           <img src={featureimg} alt="features" />
          </div>
          <blockquote className="blockquotewithborder"><p className="desc gray-color">At URSAG, We have one of the most efficient and deepest multi-solutions digital consulting teams in the industry. Our Commerce Martech services can help you deliver effective CRM solutions that increase sales. Talk to our team of CRM Experts today.</p></blockquote>
        </div>
      </div>
    </div>
  )
}


