import React, { Component } from 'react';
import '../src/css/all.css';
import './App.css';
import { Header } from './component/Header';
import Footer from './component/Footer';
import About from './component/About';
import { Contact } from './component/Contact';
import { Section } from './component/Section';
import { SuccessStoryMenuData } from './component/SuccessStoryMenuData';
import { Provider } from './component/LinkContext';
import { HclCommerce } from './component/HclCommerce';
import { SapHybris } from './component/SapHybris';
import { Adobe } from './component/Adobe';
import { Magento } from './component/Magento';
import { OracleAtg } from './component/OracleAtg';
import { GoogleCloud } from './component/GoogleCloud';
import { SalesCloud } from './component/SalesCloud';
import { Aws } from './component/Aws';
import { MicrosoftAzure } from './component/MicrosoftAzure';

class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      isAbout: 'false',
      isContact: 'false',
      isHome: 'true',
      jsxData: <Section />
    }
  }

  linkMenuCheck(menuName) {
    switch (menuName) {
      case 'isSuccessStory':
        return this.setState({
          jsxData: <SuccessStoryMenuData />
        })
    }
  }
  menuCheck(menuName) {

    switch (menuName) {
      case 'isHome':
        return this.setState({
          jsxData: <Section />
        })
      case 'isAbout':
        return this.setState({
          jsxData: <About />
        })
      case 'isContact':
        return this.setState({
          jsxData: <Provider value={this.linkMenuCheck.bind(this)}><Contact /></Provider>
        })
      case 'isSuccessStory':
        return this.setState({
          jsxData: <Provider value={this.linkMenuCheck.bind(this)}><SuccessStoryMenuData /></Provider>
        })
      case 'isHclCommerce':
        return this.setState({
          jsxData: <HclCommerce />
        })
      case 'isSap':
        return this.setState({
          jsxData: <SapHybris />
        })
      case 'isAdobe':
        return this.setState({
          jsxData: <Adobe />
        })
      case 'isMagento':
        return this.setState({
          jsxData: <Magento />
        })
      case 'isOracle':
        return this.setState({
          jsxData: <OracleAtg />
        })
      case 'isAWS':
        return this.setState({
          jsxData: <Aws />
        })
      case 'isMicroSoft':
        return this.setState({
          jsxData: <MicrosoftAzure />
        })
      case 'isGoogleCloud':
        return this.setState({
          jsxData: <GoogleCloud />
        })
      case 'isIBM':
        return this.setState({
          jsxData: <GoogleCloud />
        })
      case 'isSalesCloud':
        return this.setState({
          jsxData: <SalesCloud />
        })
      case 'isAI':
        return this.setState({
          jsxData: <SalesCloud />
        })
    }
  }

  render() {
    return (
      <div>
        <Header menuCheckCall={this.menuCheck.bind(this)} />
        {this.state.jsxData}
        <Footer />
      </div>
    );
  }
}
export default App;



